import { RouterModule } from '@angular/router';
import { NewComponent } from './new.component';

export const NewRoute = [
    {
        path: '',
        redirectTo: 'index',
        pathMatch: 'full'
    },
    {
        path: 'index',
        component:NewComponent
    }
];


